*,
 *::before,
 *::after {
     padding: 0;
     margin: 0;
     box-sizing: border-box;
 }

 
body {
  min-height: 100vh;
  width: 100%;
  overflow: auto;
  font-family: 'Poppins', sans-serif;
   background-image: linear-gradient(45deg, #242423ec, #1b1b1aec), url(https://images.pexels.com/photos/267507/pexels-photo-267507.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@import url("https://fonts.googleapis.com/css?family=Raleway");

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
