body {
  font-family: 'Open Sans', sans-serif;
  background-image: linear-gradient(to right bottom, #641da7c7, #5a2fb1d3, #921eafd0);
  background-position: center;
  background-size: cover;
  height: 100vh;
  /* font-size: 1.8rem; */
  background-attachment: fixed; 
  position: relative;   
}



.container {
    width: 70%;
    height: 80%;
    background-color: #2a0b41d9;
    margin: 0 auto;
    border-radius: .5em;
    box-shadow: .2em .3em .6em #000;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
}

@media screen and (max-width: 900px) {
  .container {
    width: 85%;
    height: 90%;
    height: max-content;
  
  }
}


.image1 {
  height: 350px;
  max-width: 800px;
  width: 100%;
  margin-top:1em;
  margin-left: -1em;
}

@media only screen and (max-width: 900px) {
  .image1 {
    display: none;
  }
}

@media only screen and (min-width: 901px) and (max-width:1199px) {
   .image1 {
  max-width: 350px;
  height: 300px;
  margin-top: -1em;

}   
}

.container-sub2 {
  text-align: center;
  max-width: 500px; 
  width: 100%;
  margin-right: 2rem;
}

@media only screen and (max-width: 900px) {
  .container-sub2 {
    width: auto;
    margin: auto;
    text-align: center;
    position: absolute;
  }
}

@media only screen and (min-width: 901px) and (max-width:1199px) {
  .container-sub2 {
    
    margin-left: -4em;
    width: 75%;
}   
}

.header-container{
  display: flex;
  align-items: center;
  margin-top: -1em;
  justify-content: center;
  flex-direction: column;
  max-width: 500px;
  width: 100%;

}

.logo {
  width: 50px;
  height: 50px; 
}

@media only screen and (max-width: 500px) {
  .logo {
    width: 40px;
    height: 40px;
  }
}

.header1 {
  color: #eee;
  font-size: 2.45rem;
  font-family: 'PT Sans Narrow', sans-serif;
  letter-spacing: .25rem;
  text-shadow: 1px 2px 3px #000;
}

@media only screen and (max-width: 350px) {
  .header1 {
    font-size: 1.4rem;
    letter-spacing: .1rem;
  }
}

@media only screen and (min-width: 351px) and (max-width:900px) {
  .header1 {
    font-size: 1.9rem;
    letter-spacing: .15rem;
  }
}

@media only screen and (min-width: 901px) and (max-width:1199px) {
  .header1 {
    font-size: 2rem;
    letter-spacing: .15rem;
  }   
}

.header2{
  color: #fff;
  text-shadow: 0 1px 2px #000;
  font-size: 1rem;
   margin-top: .1em; 
}

@media only screen and (max-width: 350px) {
  .header2 {
    font-size: .6rem;
  }
}
@media only screen and (min-width: 351px) and (max-width: 900px){
  .header2 {
    font-size: .8rem;
  }
}

form {
  margin-right: .8em;
  padding: .5em;
  margin-top: .4em;
  

}

input {
  padding: .7em;
  margin: 1em 0 1em .3em;
  width: 100%;
  max-width: 400px;
  border-radius: 3px;
  box-shadow: .5px 1px 2px #000;
  border-style: none;
  font-size: 1rem;
}
@media only screen and (max-width: 500px) {
  input {
    padding: .6em;
    font-size: .9rem;
  }
}

input:focus{
  outline: none;
  border-bottom: 3px solid #ecae10; 
}
 
button {
   background-color: #ecae10;
   color: #fff;
   box-shadow: .5px 1px 2px #000;
   width: 200px;
   font-size: 1.1rem;
   margin-top: .6em;
   padding: .5em;
   border-radius: 4px;
   border-style: none;
   text-align: center;
   
   transition: all .2s linear ;
}

button:hover  {
  background-color: #5c4407;
  transform: translateY(3px); 
}

@media only screen and (max-width: 500px) {
  button {
    width: 140px;
   font-size: 1rem;
   padding: .6em;
  }
}

.errors {
  color: #eee;
  font-size: .9rem;
  margin-top: -.9em;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .errors {
    font-size: .7rem;
  }
}
